'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { auth } from '@/lib/firebase';
import { onAuthStateChanged } from 'firebase/auth';
import LogoutButton from '@/components/LogoutButton';
import UserProfileCard from '@/components/UserProfileCard';
import Script from 'next/script';

declare global {
  interface Window {
    Paddle: any;
  }
}

export default function Dashboard() {
  const router = useRouter();
  const [user, setUser] = useState(auth.currentUser);
  const [paddleLoaded, setPaddleLoaded] = useState(false);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (!user) {
        router.replace('/login');
        return;
      }
      setUser(user);
    });

    return () => unsubscribe();
  }, [router]);

  const handleSubscription = (priceId: string) => {
    if (!paddleLoaded) {
      console.error('Paddle is not loaded yet');
      return;
    }

    window.Paddle.Checkout.open({
      settings: {
        displayMode: 'overlay',
        theme: 'light',
      },
      items: [{
        priceId: priceId,
        quantity: 1
      }],
      customer: {
        email: user?.email || '',
      }
    });
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <Script 
        src="https://cdn.paddle.com/paddle/v2/paddle.js"
        onLoad={() => {
          if (typeof window !== 'undefined' && window.Paddle) {
            window.Paddle.Setup({ 
              token: 'test_2c55b94e5180a69d07a0a04db62', // Replace with your actual Paddle token
              eventCallback: (data: any) => {
                // Log any Paddle events/errors
                console.log('Paddle event:', data);
              }
            });
            setPaddleLoaded(true);
          }
        }}
      />
      
      <nav className="bg-white shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <span className="text-xl font-semibold">Dashboard</span>
            <div className="flex items-center space-x-4">
              <LogoutButton className="text-gray-700 hover:text-gray-900" />
            </div>
          </div>
        </div>
      </nav>
      
      <main className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <div className="md:col-span-1">
            <UserProfileCard user={user} />
          </div>
          
          <div className="md:col-span-2 space-y-6">
            <div className="bg-white rounded-lg shadow-md p-6">
              <h2 className="text-lg font-semibold text-gray-800 mb-4">Quick Stats</h2>
              <div className="grid grid-cols-2 gap-4">
                <div className="p-4 bg-violet-50 rounded-lg">
                  <p className="text-sm text-violet-600">Login Method</p>
                  <p className="text-lg font-semibold text-violet-900">
                    {user?.providerData[0]?.providerId === 'password' 
                      ? 'Email/Password' 
                      : 'Google'}
                  </p>
                </div>
                <div className="p-4 bg-blue-50 rounded-lg">
                  <p className="text-sm text-blue-600">Last Sign In</p>
                  <p className="text-lg font-semibold text-blue-900">
                    {user?.metadata.lastSignInTime 
                      ? new Date(user.metadata.lastSignInTime).toLocaleDateString() 
                      : 'N/A'}
                  </p>
                </div>
              </div>
            </div>

            <div className="bg-white rounded-lg shadow-md p-6">
              <h2 className="text-lg font-semibold text-gray-800 mb-4">Subscription Plans</h2>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="border rounded-lg p-6 hover:shadow-lg transition-shadow">
                  <h3 className="text-xl font-semibold mb-2">Monthly Plan</h3>
                  <p className="text-gray-600 mb-4">Perfect for monthly flexibility</p>
                  <button
                    onClick={() => handleSubscription('pri_01jqpptmznh4vswj791kdk56q3')}
                    disabled={!paddleLoaded}
                    className={`w-full py-2 px-4 rounded-md transition-colors ${
                      paddleLoaded 
                        ? 'bg-blue-600 hover:bg-blue-700 text-white' 
                        : 'bg-gray-300 cursor-not-allowed text-gray-500'
                    }`}
                  >
                    {paddleLoaded ? 'Subscribe Monthly' : 'Loading...'}
                  </button>
                </div>
                <div className="border rounded-lg p-6 hover:shadow-lg transition-shadow">
                  <h3 className="text-xl font-semibold mb-2">Yearly Plan</h3>
                  <p className="text-gray-600 mb-4">Best value for long-term commitment</p>
                  <button
                    onClick={() => handleSubscription('pri_01jqppvckqd321w7vv04yadp72')}
                    disabled={!paddleLoaded}
                    className={`w-full py-2 px-4 rounded-md transition-colors ${
                      paddleLoaded 
                        ? 'bg-green-600 hover:bg-green-700 text-white' 
                        : 'bg-gray-300 cursor-not-allowed text-gray-500'
                    }`}
                  >
                    {paddleLoaded ? 'Subscribe Yearly' : 'Loading...'}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
